@font-face {
  font-family: "Roboto-Regular";
  src: url("../assets/fonts/Roboto/Roboto-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Roboto-BlackItalic";
  src: url("../assets/fonts/Roboto/Roboto-BlackItalic.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Roboto-Italic";
  src: url("../assets/fonts/Roboto/Roboto-Italic.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Roboto-Black";
  src: url("../assets/fonts/Roboto/Roboto-Black.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Roboto-Thin";
  src: url("../assets/fonts/Roboto/Roboto-Thin.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "Gotham-Narrow-Book";
  src: url("../assets/fonts/Gotham/Gotham-Narrow-Book.ttf") format("truetype");
  font-style: normal;
  font-weight: normal;
}
html, body {
  width: 100%;
  height: 100%;
}

body {
  font-family: "Roboto-Regular", sans-serif;
  font-size: 1rem;
  color: #172b4d;
}

.container-wrap {
  width: 100%;
  max-width: 990px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
.container-wrap--pt {
  padding-top: 32px;
}

.row-wrap {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.main-wrap {
  padding: 0 3px;
}

.flex-spacer {
  flex-grow: 1;
}

.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.no-select, .no-select * {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  -khtml-user-select: none;
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently supported by Chrome and Opera */
}

.main-board-wrap {
  background: url("~src/assets/images/default-background.jpg") center;
  background-size: cover;
  height: 100%;
}
.main-board-wrap .header {
  background: rgba(107, 119, 140, 0.7);
}

.error {
  color: #eb5a46;
  font-size: 12px;
  margin-left: 5px;
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #e4f0f6;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  width: 6px;
  height: 6px;
  background-color: #0079bf;
}

::-webkit-scrollbar-corner {
  display: none;
}

.btn {
  font-family: "Robolo-Bold", sans-serif;
  font-size: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 700;
  height: 32px;
  margin: 0 4px 0 0;
  padding: 0 8px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
  border: 0;
  transition: 0.1s ease;
  cursor: pointer;
  text-decoration: none;
  box-shadow: none;
  line-height: 32px;
  white-space: nowrap;
}
.btn__text svg {
  margin-right: 8px;
}
.btn--width {
  min-width: 32px;
}
.btn--black {
  background-color: rgba(0, 0, 0, 0.13);
}
.btn--black:hover {
  background-color: rgba(0, 0, 0, 0.23);
}
.btn--ptb8 {
  padding: 8px 0;
}
.btn--fz14 {
  font-size: 14px;
}
.btn--color-dark {
  color: #8d8d8d;
}
.btn--sign {
  margin: 0;
  background-color: #0079bf;
}
.btn--sign:hover {
  background-color: rgba(0, 121, 191, 0.8);
}
.btn--sign:focus {
  outline: none;
  background-color: #0079bf;
}